import React from 'react'
import {Link,Route,Switch} from 'react-router-dom'
import { Button,Spin, Alert ,Rate} from 'antd';
import { LeftOutlined } from '@ant-design/icons';
import Axios from 'axios'
export default class MovieDetail extends React.Component{
    constructor(props){
        super(props)
        
        this.state={
            size: 'small',
            isloading: 'true',
            info: new Object(),
            value: 0,
            valueChanged: false
           
        }
    }

    componentWillMount(){
        console.log(this.props.match.params.id)
        fetch('http://127.0.0.1:5001/movie_detail/'+
        this.props.match.params.id)
        .then(response=>{
            return response.json()
        })
        .then(data=>{
            console.log(data.url)

            this.setState({
                info:data,
                isloading:false
            })
        })
    }


    goBack=()=>{
        this.props.history.go(-1)
    }

    handleChange=(value)=>{
        this.setState({ value:value, valueChanged:true });
    }

    handleSubmit=()=>{
        alert('Rating submitted')
        this.setState({ valueChanged:false });
    }

    renderInfo(){
        if(this.state.isloading){
            return<Spin tip="Loading...">
                <Alert
                message="Requesting"
                description="A few seconds..."
                type="info"
                />
           </Spin>
           
        }else{
            console.log(this.state.info)
            return<div style={{textAlign:"center"}}>
                
                <h1>{this.state.info.title}</h1> 
                <div>General Rate: <Rate allowHalf disabled defaultValue={this.state.info.rate} /></div>
                <a href={this.state.info.url}>
                <img src={this.state.info.img} href={this.state.info.url}></img>
                </a>
                <p style={{textIndent:'2em',lineHeight:"30px"}}>Click image to view in IMDB</p>
                <p style={{textIndent:'2em',lineHeight:"30px"}}>{this.state.info.url}</p>
                <div>
                Your Rate: <Rate allowHalf  onChange={this.handleChange} value={this.state.value}/> 
                { this.state.valueChanged?<Button onClick={this.handleSubmit}>Submit</Button> : null}
                
                </div>
            </div>
        }
    }


    render(){
        return<div>
             <Button type="primary" 
             shape="round" 
             icon={<LeftOutlined/>} 
             size={this.state.size}
             onClick={this.goBack}>
                 
                Back to list
             </Button>
             {this.renderInfo()}
        </div>

    }
    
}
