import React from 'react'
import { Button,Input } from 'antd';
import Axios from 'axios'

export default class LoginContainer extends React.Component{
    constructor(props){
        super(props)
        this.state={
            login:false,
            userName:"",
            password:""
        }
    }
    
    nameChange(e){
        this.setState({
            userName:e.target.value
        })
    }

    passwordChange(e){
        this.setState({
            password:e.target.value
        })
    }

    componentWillMount(){
           
    }

    handleLogin=()=>{
        let formData = new FormData();
        formData.append('user_email', this.state.userName)
        formData.append('user_password', this.state.userName)
        const config = {
            headers: { 'content-type': `multipart/form-data` }
          }

        Axios.post(`http://127.0.0.1:5001/user/sign_in`, formData, config)
        .then((response) =>{
            var data = response.data
            console.log(data)
            localStorage.setItem("token",data['token'])
            localStorage.setItem("username",data['useremail'])
            // data = {'token','usermail','expire_timestamp'}
            this.setState({
                login:true
            })

        })
    }

    handleLogout=()=>{
        localStorage.clear();
        this.setState({
            login:false
        })
    }
    
    render(){
        if(this.state.login){
            return <div style={{"text-align":"center"}}>
                    <Button type="primary" onClick={this.handleLogout} style={{marginTop:10,width:300}}>Logout</Button>
                  </div>
        }
        return(<div style = {{"margin-top": "100"}}>
            <div style={{"text-align":"center"}}>
                <img src={require('../img/logo.png')}></img></div>
                <table style={{margin:"0 auto",width:300}}>
                    <tr>
                    <td>User Name:</td>
                    <td><Input onChange={(e)=>this.nameChange(e)} placeholder="Please input your user name" style={{width:200} }/></td>
                    </tr>
                    <tr>
                    <td>Password:</td>
                    <td><Input.Password onChange={(e)=>this.passwordChange(e)} placeholder="Please input your password" style={{width:200}}/></td>
                    </tr>
                </table>
                <div style={{"text-align":"center"}}>
                <Button type="primary" onClick={this.handleLogin} style={{marginTop:10,width:300}}>Login</Button>
                </div>
                </div>)
    }
}

