import React from 'react'

import style from '../css/item.scss'
import { Rate } from 'antd';
export default class MovieItem extends React.Component{
    constructor(props){
        super(props)

        this.state={
            
        }
    }
    componentWillMount(){
           
    }
    goDetail=()=>{
        this.props.history.push('/movie/detail/'+this.props.id)
    }
    render(){
        return<div className={style.box} onClick={this.goDetail}>
            <img src={this.props.img} style={{"width":150},{"height":210}}></img>
            <h4>Title:{this.props.title}</h4>
            <h4>Year:{this.props.year}</h4>
            <h4>Type:{this.props.genre}</h4>
            <Rate allowHalf disabled defaultValue={this.props.rate} />
        </div>
    }
    
}
